import React, { Component } from 'react';
import Card from '../Card/Card';
import HorizontalBar from '../HorizontalBar/HorizontalBar';
import OpenSourceList from './OpenSourceList.json'
import classes from './OpenSource.module.css';

class OpenSource extends Component {
  state = {
    patchList: OpenSourceList
  };

  render() {
    const { patchList } = this.state;
    const { history } = this.props;
    const ubootPatches = patchList['U-Boot'].map((patch, index) => {
      return patch.status === 'Accepted' ||
        patch.status === 'New' ||
        patch.status === 'Deferred' ? (
          <li className={classes.PatchLink} key={patch.link}>
            {index === 0 ? null : <HorizontalBar />}
            <a href={patch.link} target="_blank" rel="noopener noreferrer">
              <p>{patch.title}</p>
            </a>
          </li>
      ) : null;
    });
    return (
      <Card goBack={history.goBack} className={classes.OpenSource}>
        <h1>Open Source Contribution</h1>
        <h2 className={classes.OpenSourceProject}>
          <a href="https://github.com/u-boot/u-boot" target="_blank" rel="noopener noreferrer">
            U-Boot
          </a>
        </h2>
        <ul>{ubootPatches}</ul>
      </Card>
    );
  }
}

export default OpenSource;
